import { Fragment, useEffect, useState } from "react";
import { useFieldArray, useForm } from "react-hook-form";
import { FaSave, FaTimes } from "react-icons/fa";
import { useOutletContext } from "react-router-dom";
import Button from "../components/Button";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import TextareaInput from "../components/TextareaInput";
import TextInput from "../components/TextInput";
import { service } from "../service";
import { toast } from "react-toastify";

export default function EditInfo() {
  const { setActive } = useOutletContext();
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const { register, control, reset, handleSubmit } = useForm({
    defaultValues: {
      store_name: "",
      tagline: "",
      short_description: "",
      social: [],
    },
  });
  const { fields, append, remove } = useFieldArray({
    control,
    name: "social",
  });

  const post = (data) => {
    setSaving(true);
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .post("/store", {
              ...data,
              _method: "PUT",
            })
            .then((response) => {
              setSaving(false);
              resolve();
            })
            .catch((e) => {
              setSaving(false);
              reject();
            });
        }),
      {
        pending: "Menyimpan...",
        success: "Berhasil disimpan",
        error: "Terjadi kesalahan",
      },
      {
        toastId: "success1",
      }
    );
  };

  const getData = () => {
    setLoading(true);
    service
      .get("/store")
      .then((response) => {
        reset(response.data);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    setActive("edit");
    getData();
  }, []);

  if (loading) return <LoadingPage />;

  return (
    <Fragment>
      <Heading title="Edit Info">
        <Button
          type="button"
          className="flex items-center bg-blue-500 text-white"
          onClick={handleSubmit(post)}
          disabled={saving}
        >
          <FaSave className="mr-2" />
          Simpan
        </Button>
      </Heading>
      <form className="p-8">
        <TextInput
          type="text"
          containerClassName="mb-4"
          label="Nama Toko"
          {...register("store_name", { required: "Tidak boleh kosong" })}
        />
        <TextareaInput
          containerClassName="mb-4"
          label="Tagline"
          {...register("tagline", { required: "Tidak boleh kosong" })}
        />
        <TextareaInput
          containerClassName="mb-4"
          label="Deskripsi Singkat"
          rows={5}
          {...register("short_description", { required: "Tidak boleh kosong" })}
        />
        <div className="font-bold mb-3 pb-3 border-b">Sosial Media</div>
        {fields.map((field, index) => (
          <div className="flex mb-4 items-center" key={field.id}>
            <TextInput
              type="text"
              containerClassName="w-1/4 mr-5"
              placeholder="Instagram, Facebook dll"
              {...register(`social.${index}.name`, { required: true })}
            />
            <TextInput
              type="text"
              containerClassName="w-1/4 mr-5"
              placeholder="FaInstagram, FaFacebook, dll"
              {...register(`social.${index}.icon`, { required: true })}
            />
            <TextInput
              type="text"
              containerClassName="flex-1 mr-3"
              placeholder="Link"
              {...register(`social.${index}.link`, { required: true })}
            />
            <button
              type="button"
              onClick={() => remove(index)}
              className="w-10 h-10 mt-1 flex justify-center items-center bg-red-500 text-white rounded-sm"
            >
              <FaTimes />
            </button>
          </div>
        ))}
        <Button
          type="button"
          onClick={() => append({ key: "", value: "" })}
          className="bg-primary text-white text-sm"
        >
          + Tambah
        </Button>
      </form>
    </Fragment>
  );
}
