import { useCookies } from "react-cookie";
import { service } from "../service";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import LoadingBlock from "../components/LoadingBlock";
import { useState } from "react";

export default function Login() {
  const setCookies = useCookies(["auth"])[1];
  const navigate = useNavigate();
  const { register, handleSubmit } = useForm({
    defaultValues: {
      username: "",
      password: "",
    },
  });

  const [failed, setFailed] = useState(false);
  const [loading, setLoading] = useState(false);

  const onLogin = ({ username, password }) => {
    setLoading(true);
    service
      .get("/auth/login", {
        params: {
          username,
          password,
        },
      })
      .then((response) => {
        setFailed(false);
        setLoading(false);
        setCookies("token", response.data.token, {
          path: "/",
        });
        service.defaults.headers.common[
          "Authorization"
        ] = `Bearer ${response.data.token}`;

        navigate("/", { replace: true });
      })
      .catch(() => {
        setFailed(true);
        setLoading(false);
      });
  };

  return (
    <div className="min-h-screen w-full bg-gray-100 flex">
      <div className="w-3/5 bg-primary flex justify-center items-center">
        <img src="/logo.png" className="w-1/2 h-auto" />
      </div>
      <div className="flex-1 p-8 px-16 flex flex-col justify-center">
        <div className="font-bold text-2xl text-gray-800 kanit">Login</div>
        <p className="mb-8">
          Silahkan masuk ke akun anda untuk mengakses halaman admin
        </p>
        <form onSubmit={handleSubmit(onLogin)}>
          {failed && (
            <div className="text-center bg-red-400 mb-5 rounded-sm py-2 px-5 text-white">
              Username atau password salah
            </div>
          )}
          <input
            type="text"
            className="bg-white h-10 rounded-sm w-full block border px-3 mb-4"
            placeholder="Username"
            {...register("username", { required: true })}
          />
          <input
            type="password"
            className="bg-white h-10 rounded-sm w-full block border px-3 mb-6"
            placeholder="Password"
            {...register("password", { required: true })}
          />
          <button
            type="submit"
            className="w-full block bg-primary text-white h-10 rounded-sm"
          >
            Masuk
          </button>
        </form>
      </div>
      {loading && <LoadingBlock />}
    </div>
  );
}
