export default function SwitchInput({ value = false, onChange = () => {} }) {
  return (
    <div
      className={`rounded-full h-8 ${
        value ? "bg-green-600 border-green-700" : "bg-gray-200 border-gray-300"
      } border flex items-center relative cursor-pointer`}
      onClick={() => onChange(!value)}
    >
      <div className="w-6 h-6 rounded-full invisible" />
      <div className="w-6 h-6 rounded-full invisible" />
      <div className="absolute top-0 left-0 w-full h-full flex items-center">
        <div
          className={`w-6 h-6 bg-white transform ${
            value ? "translate-x-full" : "translate-x-0"
          } rounded-full transition-all`}
        />
      </div>
    </div>
  );
}
