import { FaSearch } from "react-icons/fa";

export default function Grid({ data = [], columns = [] }) {
  return (
    <div className="p-8 flex-1 overflow-hidden">
      <div className="flex flex-col bg-white border rounded-sm h-full">
        <div className="border-b bg-gray-50 relative p-5">
          <FaSearch />
          <input
            type="text"
            className="h-full w-full absolute top-0 left-0 block border-0 bg-transparent px-5 pl-12"
            placeholder="Cari..."
          />
        </div>
        <div className="overflow-auto w-full flex-1 relative">
          <table style={{ maxWidth: 1024 }} className="min-w-full">
            <thead className="border-b sticky top-0 left-0 w-full bg-white">
              <tr>
                <th className="w-12">NO</th>
                {columns.map((val, i) => (
                  <th className={"text-left " + val.className} key={`${i}`}>
                    {val.name}
                  </th>
                ))}
              </tr>
            </thead>
            {data.length ? (
              <tbody>
                {data.map((val, i) => (
                  <tr key={`${i}`}>
                    <td className="text-center">{i + 1}</td>
                    {columns.map((col, i) => (
                      <td key={`${i}`}>
                        {col.render ? col.render(val) : val[col.val]}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            ) : (
              <tbody>
                <tr>
                  <td colSpan={columns.length} className="text-center">
                    Tidak Ada Data
                  </td>
                </tr>
              </tbody>
            )}
          </table>
        </div>
        <div className="h-12 border-t bg-gray-50"></div>
      </div>
    </div>
  );
}
