import { Fragment, useEffect, useState } from "react";
import { useCookies } from "react-cookie";
import { Route, Routes } from "react-router-dom";
import Layout from "./components/Layout";
import ComposeProduct from "./pages/ComposeProduct";
import Dashboard from "./pages/Dashboard";
import Login from "./pages/Login";
import Product from "./pages/Product";
import { service } from "./service";
import { ToastContainer } from "react-toastify";
import Sliders from "./pages/Sliders";
import ComposeSlider from "./pages/ComposeSlider";
import Media from "./pages/Media";
import EditInfo from "./pages/EditInfo";

export default function App() {
  const [cookies] = useCookies(["auth"]);
  const [mount, setMount] = useState(false);

  useEffect(() => {
    if (cookies.token) {
      service.defaults.headers.common[
        "Authorization"
      ] = `Bearer ${cookies.token}`;
    }

    setMount(true);
  }, []);

  if (!mount) return null;

  return (
    <Fragment>
      <Routes>
        <Route path="/auth/login" element={<Login />} />
        <Route path="/" element={<Layout />}>
          <Route path="" element={<Dashboard />} />
          <Route path="product/add" element={<ComposeProduct />} />
          <Route path="product/:id" element={<ComposeProduct edit={true} />} />
          <Route path="product" element={<Product />} />
          <Route path="slider/add" element={<ComposeSlider />} />
          <Route path="slider/:id" element={<ComposeSlider edit={true} />} />
          <Route path="slider" element={<Sliders />} />
          <Route path="edit-info" element={<EditInfo />} />
          <Route path="media" element={<Media />} />
        </Route>
      </Routes>
      <ToastContainer />
    </Fragment>
  );
}
