import {
  forwardRef,
  Fragment,
  useCallback,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import { useDropzone } from "react-dropzone";
import { useFieldArray, useForm } from "react-hook-form";
import { FaTimes } from "react-icons/fa";
import { toast } from "react-toastify";
import { service } from "../service";
import Button from "./Button";

const MediaPicker = forwardRef(({ show = false }, ref) => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [ids, setIds] = useState([]);
  const [modalShow, setModalShow] = useState(show);
  const _change = useRef(() => {});
  const [multiple, setMultiple] = useState(true);
  const [uploadOnly, setUploadOnly] = useState(false);
  const [upload, setUpload] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [percentage, setPercentage] = useState(0);

  const { control, reset, handleSubmit } = useForm({
    defaultValues: {},
  });

  const { fields, append, remove } = useFieldArray({
    control,
    name: "files",
  });

  const onDrop = useCallback((acceptedFiles) => {
    append(acceptedFiles);
  }, []);
  const { getRootProps, getInputProps, isDragActive, open } = useDropzone({
    onDrop,
  });

  const postData = ({ files }) => {
    setUploading(true);
    const form = new FormData();
    for (let file of files) {
      form.append("files[]", file);
    }

    service
      .post("/media", form, {
        onUploadProgress: (progressEvent) => {
          let percentComplete = progressEvent.loaded / progressEvent.total;
          percentComplete = Math.floor(percentComplete * 100);
          setPercentage(percentComplete);
        },
      })
      .then(() => {
        setUploading(false);
        setPercentage(0);
        reset({});
        toast.success("Berhasil mengupload file");

        if (uploadOnly) {
          setModalShow(false);
          _change.current();
        } else {
          setUpload(false);
          getData();
        }
      })
      .catch(() => {
        toast.error("Terjadi kesalahan");
        setUploading(false);
        setPercentage(0);
      });
  };

  const getData = () => {
    setLoading(true);
    service
      .get("/media")
      .then((response) => {
        setLoading(false);
        setData(response.data.data);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    getData();
  }, []);

  useEffect(() => {
    if (!modalShow) {
      setIds([]);
      _change.current = () => {};
    }
  }, [modalShow]);

  useImperativeHandle(
    ref,
    () => ({
      toggle: (onChange = () => {}, multiple = true, uploadOnly = false) => {
        _change.current = onChange;
        setMultiple(multiple);
        setUploadOnly(uploadOnly);
        setModalShow((val) => !val);
      },
    }),
    []
  );

  return (
    <Fragment>
      {modalShow && (
        <div className="bg-black bg-opacity-75 fixed top-0 left-0 w-screen h-screen z-20"></div>
      )}
      <div
        className={
          "flex justify-center items-center fixed top-0 left-0 w-screen h-screen p-10 z-20 transform transition-all duration-300 " +
          (modalShow ? "translate-y-0" : "-translate-y-full")
        }
      >
        <div className="w-full h-full bg-white rounded-sm flex flex-col">
          <div className="border-b py-3 px-5 flex items-center">
            {!uploadOnly && (
              <button
                type="button"
                className={`${
                  !upload
                    ? "bg-primary text-white border-transparent"
                    : "bg-white hover:bg-gray-100 border-gray-200"
                } rounded-md mr-3 py-2 px-3 border `}
                onClick={() => setUpload(false)}
              >
                Pilih Media
              </button>
            )}
            <button
              type="button"
              className={`${
                upload || uploadOnly
                  ? "bg-primary text-white border-transparent"
                  : "bg-white hover:bg-gray-100 border-gray-200"
              } rounded-md py-2 px-3 border `}
              onClick={() => setUpload(true)}
            >
              Upload
            </button>
            {upload || uploadOnly ? (
              <button
                type="button"
                className={`bg-blue-100 border-blue-300 rounded-md py-2 px-3 border ml-auto`}
                onClick={() => open()}
              >
                Pilih File
              </button>
            ) : null}
          </div>
          {loading ? (
            <div className="flex-1 w-full flex items-center justify-center">
              <div className="text-primary lds-dual-ring"></div>
            </div>
          ) : (
            <div className="h-full overflow-auto relative">
              {upload || uploadOnly ? (
                <Fragment>
                  <div
                    {...getRootProps()}
                    {...(uploading ? { onClick: () => {} } : {})}
                    className={`w-full h-full overflow-auto border-2 border-dashed bg-opacity-40 ${
                      isDragActive
                        ? "bg-blue-300 border-blue-300"
                        : "bg-transparent border-transparent"
                    }`}
                  >
                    <input {...getInputProps()} />
                    {fields.length ? (
                      <div className="flex flex-col">
                        {fields.map((val, i) => (
                          <div
                            className="py-3 px-5 border-b flex justify-between items-center"
                            key={`${i}`}
                            onClick={(e) => e.stopPropagation()}
                          >
                            {val.path}
                            <button
                              onClick={() => {
                                remove(i);
                              }}
                              type="button"
                              className="text-white flex justify-center items-center w-6 h-6 bg-red-400 rounded-sm"
                            >
                              <FaTimes />
                            </button>
                          </div>
                        ))}
                      </div>
                    ) : (
                      <div className="w-full h-full flex items-center justify-center">
                        <div className="text-center">
                          Drop atau klik untuk memilih file
                        </div>
                      </div>
                    )}
                  </div>
                </Fragment>
              ) : data.length ? (
                <div className="w-full grid grid-flow-row grid-cols-6 gap-5 p-5">
                  {data.map((val, i) => {
                    const selectedIndex = ids.findIndex(
                      (pre) => val.id === pre.id
                    );
                    return (
                      <div
                        key={`${i}`}
                        className={`cursor-pointer rounded-sm border-2 ${
                          selectedIndex >= 0
                            ? "border-primary"
                            : "border-gray-300"
                        }`}
                        onClick={() => {
                          setIds((data) => {
                            let ids = [...data];
                            if (selectedIndex >= 0) {
                              ids.splice(selectedIndex, 1);
                            } else if (multiple) {
                              ids.push(val);
                            } else {
                              ids = [val];
                            }

                            return [...ids];
                          });
                        }}
                      >
                        <div className="w-1-1">
                          <img src={val.url} />
                          {selectedIndex >= 0 && (
                            <div className="w-8 h-8 m-2 bg-primary rounded-full absolute top-0 right-0 text-white flex justify-center items-center border border-white text-xs">
                              {selectedIndex + 1}
                            </div>
                          )}
                        </div>
                      </div>
                    );
                  })}
                </div>
              ) : (
                <div className="w-full h-full flex justify-center items-center">
                  Belum ada media tersimpan
                </div>
              )}
            </div>
          )}
          <div className="border-t flex p-5 items-center">
            {uploading ? (
              <div className="w-1/4 bg-gray-200 h-2">
                <div
                  className="bg-yellow-500 h-2 transition-all"
                  style={{ width: `${percentage}%` }}
                ></div>
              </div>
            ) : null}
            <Button
              type="button"
              className="bg-gray-300 ml-auto mr-3"
              onClick={() => {
                setModalShow(false);
              }}
            >
              Batal
            </Button>
            {upload || uploadOnly ? (
              <Button
                type="button"
                disabled={!fields.length || uploading}
                onClick={handleSubmit(postData)}
                className="bg-primary text-white disabled:bg-gray-600"
              >
                Upload File
              </Button>
            ) : (
              <Button
                type="button"
                disabled={!ids.length}
                onClick={() => {
                  _change.current(ids);
                  setModalShow(false);
                }}
                className="bg-primary text-white disabled:bg-gray-600"
              >
                Pilih
              </Button>
            )}
          </div>
        </div>
      </div>
    </Fragment>
  );
});

export default MediaPicker;
