import { Fragment, useEffect, useRef, useState } from "react";
import { FaPlusSquare, FaTrash } from "react-icons/fa";
import { useOutletContext } from "react-router-dom";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import { service } from "../service";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { toast } from "react-toastify";
import MediaPicker from "../components/MediaPicker";
import Button from "../components/Button";

const MySwal = withReactContent(Swal);

export default function Media() {
  const { setActive } = useOutletContext();
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const _mediaRef = useRef();

  const getData = () => {
    setLoading(true);
    service
      .get("/media")
      .then((response) => {
        setData(response.data.data);
        setLoading(false);
      })
      .catch(() => {});
  };

  const onDelete = (id) => {
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .delete(`/media/${id}`)
            .then(() => {
              getData();
              resolve();
            })
            .catch(() => {
              setLoading(false);
              reject();
            });
        }),
      {
        pending: "Menghapus...",
        success: "Berhasil dihapus",
        error: "Terjadi kesalahan",
      }
    );
  };

  useEffect(() => {
    setActive("media");
    getData();
  }, []);

  if (loading) return <LoadingPage />;

  if (!loading && !data.length)
    return (
      <div className="h-screen flex flex-col">
        <Heading title="Media">
          <Button
            type="button"
            className="flex items-center bg-blue-500 text-white"
            onClick={() =>
              _mediaRef.current.toggle(
                () => {
                  getData();
                },
                true,
                true
              )
            }
          >
            <FaPlusSquare className="mr-2" />
            Tambah
          </Button>
        </Heading>
        <div className="flex-1 flex justify-center items-center">
          Belum ada media disimpan
        </div>

        <MediaPicker ref={_mediaRef} />
      </div>
    );
  return (
    <Fragment>
      <Heading title="Media">
        <Button
          type="button"
          className="flex items-center bg-blue-500 text-white"
          onClick={() =>
            _mediaRef.current.toggle(
              () => {
                getData();
              },
              true,
              true
            )
          }
        >
          <FaPlusSquare className="mr-2" />
          Tambah
        </Button>
      </Heading>

      <div className="grid grid-flow-row grid-cols-4 gap-5 p-5">
        {data.map((val, i) => (
          <div className="bg-white p-1 rounded-sm relative">
            <div className="w-1-1">
              <img src={val.url} />
            </div>
            <div className="p-5 text-center text-xs">{val.name}</div>
            <div className="transition-all absolute top-0 left-0 w-full h-full bg-black bg-opacity-0 hover:bg-opacity-50 rounded-sm group">
              <button
                type="button"
                className="py-2 px-5 bg-white rounded-sm invisible group-hover:visible absolute top-0 right-0 m-1"
                onClick={() => {
                  MySwal.fire({
                    title: "Anda Yakin?",
                    html: `Anda akan menghapus media <b>${val.name}</b>`,
                    icon: "warning",
                    showCancelButton: true,
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, lanjutkan",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      onDelete(val.id);
                    }
                  });
                }}
              >
                <FaTrash />
              </button>
            </div>
          </div>
        ))}
      </div>
      <MediaPicker ref={_mediaRef} />
    </Fragment>
  );
}
