import { useEffect, useState } from "react";
import { FaPen, FaPlusSquare, FaTrash } from "react-icons/fa";
import { Link, useNavigate, useOutletContext } from "react-router-dom";
import { toast } from "react-toastify";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import Button from "../components/Button";
import Grid from "../components/Grid";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import { service } from "../service";

const MySwal = withReactContent(Swal);

export default function Sliders() {
  const { setActive } = useOutletContext();
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const navigate = useNavigate();

  const getData = () => {
    setLoading(true);
    service
      .get("/slider")
      .then((response) => {
        setData(response.data.data);
        setLoading(false);
      })
      .catch(() => {});
  };

  const onDelete = (id) => {
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .delete(`/slider/${id}`)
            .then(() => {
              getData();
              resolve();
            })
            .catch(() => {
              setLoading(false);
              reject();
            });
        }),
      {
        pending: "Menghapus...",
        success: "Berhasil dihapus",
        error: "Terjadi kesalahan",
      }
    );
  };

  const columns = [
    {
      val: "name",
      name: "Nama",
    },
    {
      val: "img",
      name: "Gambar",
      render: (data) => (
        <div className="w-32">
          <div className="w-16-9">
            <img src={data.image.url} />
          </div>
        </div>
      ),
    },
    {
      val: "url",
      name: "URL",
    },
    {
      val: "action",
      name: "",
      className: "w-20",
      render: (data) => (
        <div className="flex">
          <Link
            to={"/slider/" + data.id}
            className="p-2 text-gray-400 hover:text-gray-700"
          >
            <FaPen />
          </Link>
          <button
            type="button"
            className="p-2 text-gray-400 hover:text-gray-700"
            onClick={() => {
              MySwal.fire({
                title: "Anda Yakin?",
                html: `Anda akan menghapus slider <b>${data.name}</b>`,
                icon: "warning",
                showCancelButton: true,
                cancelButtonColor: "#d33",
                confirmButtonText: "Yes, lanjutkan",
              }).then((result) => {
                if (result.isConfirmed) {
                  onDelete(data.id);
                }
              });
            }}
          >
            <FaTrash />
          </button>
        </div>
      ),
    },
  ];

  useEffect(() => {
    getData();
    setActive("sliders");
  }, []);

  if (loading) return <LoadingPage />;

  return (
    <div className="flex flex-col h-screen overflow-hidden">
      <Heading title="Data Slider">
        <Button
          type="button"
          className="flex items-center bg-blue-500 text-white"
          onClick={() => navigate("/slider/add")}
        >
          <FaPlusSquare className="mr-2" />
          Tambah
        </Button>
      </Heading>
      <Grid data={data} columns={columns} />
    </div>
  );
}
