import { useEffect, useState } from "react";
import { FaPen, FaPlusSquare, FaTrash } from "react-icons/fa";
import { Link, useNavigate, useOutletContext } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import Button from "../components/Button";
import Grid from "../components/Grid";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import { service } from "../service";

const MySwal = withReactContent(Swal);

export default function Product() {
  const { setActive } = useOutletContext();
  const [data, setData] = useState([]);
  const navigate = useNavigate();

  const [loading, setLoading] = useState(true);

  const getData = () => {
    setLoading(true);
    service
      .get("/product")
      .then((response) => {
        setData(response.data.data);
        setLoading(false);
      })
      .catch(() => {});
  };

  const onDelete = (id) => {
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .delete(`/product/${id}`)
            .then(() => {
              getData();
              resolve();
            })
            .catch(() => {
              setLoading(false);
              reject();
            });
        }),
      {
        pending: "Menghapus...",
        success: "Berhasil dihapus",
        error: "Terjadi kesalahan",
      }
    );
  };

  const columns = [
    {
      val: "product_name",
      name: "Nama Produk",
    },
    {
      val: "price",
      name: "Harga",
    },
    {
      val: "slug",
      name: "Slug",
    },
    {
      val: "publish",
      name: "Publish",
      render: (data) => (
        <div
          className={
            "p-2 text-white rounded text-center text-xs " +
            (data.publish ? "bg-green-500" : "bg-red-500")
          }
        >
          {data.publish ? "TRUE" : "FALSE"}
        </div>
      ),
    },
    {
      val: "action",
      name: "",
      className: "w-20",
      render: (data) => (
        <div className="flex">
          <Link
            to={"/product/" + data.id}
            className="p-2 text-gray-400 hover:text-gray-700"
          >
            <FaPen />
          </Link>
          <button
            type="button"
            className="p-2 text-gray-400 hover:text-gray-700"
            onClick={() => {
              MySwal.fire({
                title: "Anda Yakin?",
                html: `Anda akan menghapus produk <b>${data.product_name}</b>`,
                icon: "warning",
                showCancelButton: true,
                cancelButtonColor: "#d33",
                confirmButtonText: "Yes, lanjutkan",
              }).then((result) => {
                if (result.isConfirmed) {
                  onDelete(data.id);
                }
              });
            }}
          >
            <FaTrash />
          </button>
        </div>
      ),
    },
  ];

  useEffect(() => {
    setActive("product");
    getData();
  }, []);

  if (loading) return <LoadingPage />;
  return (
    <div className="flex flex-col h-screen overflow-hidden">
      <Heading title="Data Produk">
        <Button
          type="button"
          className="flex items-center bg-blue-500 text-white"
          onClick={() => navigate("/product/add")}
        >
          <FaPlusSquare className="mr-2" />
          Tambah
        </Button>
      </Heading>
      <Grid data={data} columns={columns} />
    </div>
  );
}
