import { useEffect, useState } from "react";
import { useCookies } from "react-cookie";
import {
  FiHome,
  FiBox,
  FiLayers,
  FiImage,
  FiLogOut,
  FiGlobe,
} from "react-icons/fi";
import { Link, useLocation, useNavigate, Outlet } from "react-router-dom";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { service } from "../service";
import LoadingBlock from "./LoadingBlock";

const SidebarLink = ({ icon, children, active = false, ...props }) => {
  const Icon = icon || FiHome;

  return (
    <li className="px-2 relative">
      {active && (
        <div className="absolute top-0 right-0 h-full w-1 bg-primary rounded-l-sm"></div>
      )}
      <Link
        className="bg-transparent hover:bg-gray-900 text-gray-600 hover:text-white py-3 rounded-sm flex items-center"
        {...props}
      >
        <Icon className="w-12" /> {children}
      </Link>
    </li>
  );
};

const MySwal = withReactContent(Swal);

export default function Layout() {
  const location = useLocation();
  const [cookies, setCookies, removeCookies] = useCookies(["auth"]);
  const navigate = useNavigate();
  const [active, setActive] = useState("dashboard");
  const [loading, setLoading] = useState(false);

  const logout = () => {
    setLoading(true);
    service
      .delete("/auth/logout")
      .then((response) => {
        setLoading(false);
        removeCookies("token", {
          path: "/",
        });
        service.defaults.headers.common["Authorization"] = undefined;

        navigate("/auth/login");
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    if (!cookies.token) {
      navigate("/auth/login", {
        state: {
          from: location,
        },
        replace: true,
      });
    }
  }, [cookies]);
  return (
    <div className="bg-gray-100 min-h-screen">
      <div className="flex">
        <div className="w-64"></div>
        <div className="flex-1 min-h-screen">
          <Outlet context={{ active, setActive }} />
        </div>
      </div>
      <div className="w-64 bg-white h-screen border-r fixed top-0 left-0">
        <div className="text-center h-16 bg-gray-50 flex justify-center items-center border-b">
          <div className="text-lg kanit font-bold text-primary">
            Admin Panel
          </div>
        </div>
        <ul className="flex flex-col py-2">
          <SidebarLink to="/" active={active === "dashboard"}>
            Dashboard
          </SidebarLink>
          <SidebarLink to="/product" icon={FiBox} active={active === "product"}>
            Data Produk
          </SidebarLink>
          <SidebarLink
            to="/slider"
            icon={FiLayers}
            active={active === "sliders"}
          >
            Data Sliders
          </SidebarLink>
          <SidebarLink to="/media" icon={FiImage} active={active === "media"}>
            Media
          </SidebarLink>
          <SidebarLink
            to="/edit-info"
            icon={FiGlobe}
            active={active === "edit"}
          >
            Edit Info
          </SidebarLink>
          <SidebarLink
            to="/"
            icon={FiLogOut}
            onClick={(e) => {
              e.preventDefault();
              MySwal.fire({
                title: "Anda Yakin?",
                html: `Anda akan keluar dari halaman admin`,
                icon: "warning",
                showCancelButton: true,
                cancelButtonColor: "#d33",
                confirmButtonText: "Yes, lanjutkan",
              }).then((result) => {
                if (result.isConfirmed) {
                  logout();
                }
              });
            }}
          >
            Keluar
          </SidebarLink>
        </ul>
      </div>
      {loading ? <LoadingBlock /> : null}
    </div>
  );
}
