import { Fragment, useEffect, useRef, useState } from "react";
import { Controller, useFieldArray, useForm } from "react-hook-form";
import { FaSave, FaTimes } from "react-icons/fa";
import "react-quill/dist/quill.snow.css";
import { useNavigate, useOutletContext, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import Button from "../components/Button";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import MediaPicker from "../components/MediaPicker";
import RichText from "../components/RichText";
import SwitchInput from "../components/SwitchInput";
import TextInput from "../components/TextInput";
import { service } from "../service";

export default function ComposeProduct({ edit = false }) {
  const { id } = useParams();
  const [loading, setLoading] = useState(edit ? true : false);
  const [saving, setSaving] = useState(false);
  const navigate = useNavigate();
  const { setActive } = useOutletContext();
  const { register, control, reset, handleSubmit } = useForm({
    defaultValues: {
      product_name: "",
      price: "",
      price_before: "",
      description: "",
      publish: false,
    },
  });
  const {
    fields: specFields,
    append: specAppend,
    remove: specRemove,
  } = useFieldArray({
    control,
    name: "spec",
  });
  const {
    fields: imageFields,
    append: imageAppend,
    remove: imageRemove,
  } = useFieldArray({
    control,
    name: "images",
  });

  const _mediaRef = useRef();

  const getData = () => {
    setLoading(true);
    service
      .get("/product/" + id)
      .then((response) => {
        reset(response.data);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const post = ({ images = [], ...data }) => {
    setSaving(true);
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .post(edit ? "/product/" + id : "/product", {
              ...data,
              images: images.map((val, i) => ({ id: val.id, position: i })),
              _method: edit ? "PUT" : "POST",
            })
            .then((response) => {
              setSaving(false);
              resolve();
              if (!edit) {
                navigate("/product/" + response.data.id, { replace: true });
              }
            })
            .catch((e) => {
              setSaving(false);
              reject();
            });
        }),
      {
        pending: "Menyimpan...",
        success: "Berhasil disimpan",
        error: "Terjadi kesalahan",
      },
      {
        toastId: "success1",
      }
    );
  };

  useEffect(() => {
    setActive("product");
  }, []);

  useEffect(() => {
    if (id) {
      getData();
    }
  }, []);

  if (loading) return <LoadingPage />;
  return (
    <Fragment>
      <Heading title={edit ? "Edit Produk" : "Tambah Produk"}>
        <div className="flex items-center h-full">
          <div className="flex items-center mr-6 h-full border-r border-l px-6">
            <span className="text-xs mr-2">Publish</span>
            <Controller
              control={control}
              name="publish"
              render={({ field: { value, onChange } }) => (
                <SwitchInput value={value} onChange={onChange} />
              )}
            />
          </div>
          <Button
            type="button"
            className="flex items-center bg-blue-500 text-white"
            onClick={handleSubmit(post)}
            disabled={saving}
          >
            <FaSave className="mr-2" />
            Simpan
          </Button>
        </div>
      </Heading>
      <form className="p-8">
        <div className="font-bold mb-3 pb-3 border-b">Informasi Dasar</div>
        <TextInput
          type="text"
          containerClassName="mb-4"
          label="Nama Produk"
          {...register("product_name", { required: "Tidak boleh kosong" })}
        />
        <TextInput
          type="number"
          containerClassName="mb-4"
          label="Harga"
          {...register("price", { required: "Tidak boleh kosong" })}
        />
        <TextInput
          type="number"
          containerClassName="mb-4"
          label="Harga Coret"
          {...register("price_before")}
        />
        <Controller
          control={control}
          name="description"
          rules={{ required: "Tidak boleh kosong" }}
          render={({ field: { value, onChange } }) => (
            <RichText
              value={value}
              onChange={onChange}
              label="Deskripsi"
              containerClassName="mb-4"
            />
          )}
        />
        <div className="font-bold mb-3 pb-3 border-b">Spesifikasi</div>
        {specFields.map((field, index) => (
          <div className="flex mb-4 items-center" key={field.id}>
            <TextInput
              type="text"
              containerClassName="flex-1 mr-5"
              placeholder="Bahan, Sablon, dll"
              {...register(`spec.${index}.key`, { required: true })}
            />
            <TextInput
              type="text"
              containerClassName="flex-1 mr-3"
              {...register(`spec.${index}.value`, { required: true })}
            />
            <button
              type="button"
              onClick={() => specRemove(index)}
              className="w-10 h-10 mt-1 flex justify-center items-center bg-red-500 text-white rounded-sm"
            >
              <FaTimes />
            </button>
          </div>
        ))}
        <Button
          type="button"
          onClick={() => specAppend({ key: "", value: "" })}
          className="bg-primary text-white text-sm"
        >
          + Tambah
        </Button>
        <div className="font-bold mb-3 pb-3 border-b mt-8">Gambar</div>
        {imageFields.length ? (
          <div className="grid grid-flow-row grid-cols-8 gap-5 mb-4">
            {imageFields.map((val, i) => (
              <div className="w-1-1 group" key={`${i}`}>
                <img src={val.url} />
                <button
                  onClick={() => {
                    imageRemove(i);
                  }}
                  type="button"
                  className="text-white invisible group-hover:visible flex justify-center items-center absolute top-0 right-0 w-6 h-6 bg-red-500 m-1 rounded-sm"
                >
                  <FaTimes />
                </button>
              </div>
            ))}
          </div>
        ) : null}
        <Button
          type="button"
          className="bg-primary text-white text-sm"
          onClick={() =>
            _mediaRef.current.toggle((images) => {
              imageAppend(images);
            })
          }
        >
          + Tambah
        </Button>
        <MediaPicker ref={_mediaRef} />
      </form>
    </Fragment>
  );
}
