import { Fragment, useEffect, useRef, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FaSave } from "react-icons/fa";
import "react-quill/dist/quill.snow.css";
import { useNavigate, useOutletContext, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import Button from "../components/Button";
import Heading from "../components/Heading";
import LoadingPage from "../components/LoadingPage";
import MediaPicker from "../components/MediaPicker";
import TextInput from "../components/TextInput";
import { service } from "../service";

export default function ComposeSlider({ edit = false }) {
  const { id } = useParams();
  const [loading, setLoading] = useState(edit ? true : false);
  const [saving, setSaving] = useState(false);
  const navigate = useNavigate();
  const { setActive } = useOutletContext();
  const { register, control, reset, handleSubmit } = useForm({
    defaultValues: {
      name: "",
      media_id: {},
      url: "",
    },
  });

  const _mediaRef = useRef();

  const getData = () => {
    setLoading(true);
    service
      .get("/slider/" + id)
      .then((response) => {
        reset({ ...response.data, media_id: response.data.image });
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const post = ({ media_id = {}, ...data }) => {
    setSaving(true);
    toast.promise(
      () =>
        new Promise((resolve, reject) => {
          service
            .post(edit ? "/slider/" + id : "/slider", {
              ...data,
              media_id: media_id.id,
              _method: edit ? "PUT" : "POST",
            })
            .then((response) => {
              setSaving(false);
              resolve();
              if (!edit) {
                navigate("/slider/" + response.data.id, { replace: true });
              }
            })
            .catch((e) => {
              setSaving(false);
              reject();
            });
        }),
      {
        pending: "Menyimpan...",
        success: "Berhasil disimpan",
        error: "Terjadi kesalahan",
      },
      {
        toastId: "success1",
      }
    );
  };

  useEffect(() => {
    setActive("sliders");
  }, []);

  useEffect(() => {
    if (id) {
      getData();
    }
  }, []);

  if (loading) return <LoadingPage />;
  return (
    <Fragment>
      <Heading title={edit ? "Edit Slider" : "Tambah Slider"}>
        <Button
          type="button"
          className="flex items-center bg-blue-500 text-white"
          onClick={handleSubmit(post)}
          disabled={saving}
        >
          <FaSave className="mr-2" />
          Simpan
        </Button>
      </Heading>
      <form className="p-8">
        <TextInput
          type="text"
          containerClassName="mb-4"
          label="Nama"
          {...register("name", { required: "Tidak boleh kosong" })}
        />
        <TextInput
          type="text"
          containerClassName="mb-4"
          label="URL"
          {...register("url")}
        />
        <Controller
          control={control}
          name="media_id"
          rules={{ required: "Tidak boleh kosong" }}
          render={({ field: { value, onChange } }) => (
            <Fragment>
              <div className="mb-4">
                <label>Gambar</label>
                {value?.url ? (
                  <div className="w-16-9 mt-1">
                    <img src={value.url} />
                  </div>
                ) : null}
              </div>
              <Button
                type="button"
                className="bg-primary text-white text-sm"
                onClick={() =>
                  _mediaRef.current.toggle((images) => {
                    onChange(images[0]);
                  }, false)
                }
              >
                Pilih Media
              </Button>
            </Fragment>
          )}
        />
        <MediaPicker ref={_mediaRef} />
      </form>
    </Fragment>
  );
}
