import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";

export default function RichText({
  containerClassName,
  label,
  className,
  ...props
}) {
  return (
    <div className={containerClassName}>
      {label && <label>{label}</label>}
      <ReactQuill
        theme="snow"
        className={"mt-1 bg-white " + className}
        {...props}
      />
    </div>
  );
}
